import { Component, h, Prop, EventEmitter, Event } from '@stencil/core';
import { IListItem } from '../../types/ListItem';
import { ListItem, ListSeparator } from '@limetech/lime-elements';

@Component({
    tag: 'template-list',
    shadow: true,
    styleUrl: 'template-list.scss',
})
export class TemplateList {
    @Prop() public templates: Array<ListItem | ListSeparator>;
    @Prop() public selectedTemplate: IListItem;
    @Prop() public isLoading: boolean;

    @Event() setTemplate: EventEmitter;

    constructor() {
        this.selectTemplate = this.selectTemplate.bind(this);
    }

    public render() {
        if (this.isLoading) {
            return <ga-loader />;
        }

        if (!this.templates.length) {
            return <empty-state text="No templates were found!" />;
        }

        return (
            <limel-list
                class="accordion-content"
                items={this.templates}
                type="selectable"
                onChange={this.selectTemplate}
            />
        );
    }

    private selectTemplate(event) {
        this.setTemplate.emit(event.detail);
    }
}
